import { Component } from '@angular/core';
import { IonicPage, NavParams, ViewController, AlertController } from 'ionic-angular';

/**
 * Generated class for the SelectFloorNoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-select-floor-no',
  templateUrl: 'select-floor-no.html',
})
export class SelectFloorNoPage {
  floorNo: number;
  constructor(public navParams: NavParams, public viewCtrl: ViewController, private alertCtrl: AlertController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SelectFloorNoPage');
  }

  dismiss(param) {
    this.viewCtrl.dismiss(param?param:'');
  }

  submit() {
    if(this.floorNo) {
      this.dismiss(this.floorNo);
    } else {
      this.presentAlert('输入不合法', '请输入门牌号');
    } 
  }

  presentAlert(title: string, subTitle: string) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: subTitle,
      buttons: ['确定']
    });
    alert.present();
  }
}



